/*
=============== 
Global Styles
===============
*/
/* 
-FONT SIZE
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98  

-SPACING SYSTEM (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128

*/

:root {
  /* root letter page */
  --letter-bg-color-pink: '#FFF3F5';

  /* --letter-main-color: #91ada3; */
  --border-section-main: 1px solid rgba(145, 173, 163, 0.5);
  --border-section-1: 2px solid rgba(243, 135, 35, 0.1);
  --orange-color: rgba(243, 135, 35, 1);
  --letter-main-color: rgba(145, 173, 163, 1);
  --gray-item-color: 1px solid #c6c6c6;
  --border-img-dash: 2px dashed #828282;
  --color-hover: #cf9897;
  --color-red: #ff6f61;
  --color-input: #e2e8f0;
  /* root home page */
  --pink--bold: #f5eced;
  --pink-color-1: rgba(212, 150, 150, 1);

  --pink-color-2: #fbf3f1;
  --black-color: #000;
  --white-color: #fff;
  --gray-color: #c1c1c1;
  --gray-color-2: #f1f5f9;
  --gray-color-3: #94a3b8;
  --gray-color-4: #f4f4f4;
  --panel-color: #94a3b8;
  --text-color-head: #0f172a;
  --text-color-darkmode: #131825;
  --quick-sand-font-family: 'Quicksand', sans-serif;
  --lateef-font-family: 'Lateef', serif;
  --wind-song-font-family: 'WindSong', cursive;
  --font-size: 60px;
  --font-size-default: 16px;
  --red-color-mess-err: #ff0000;
  --bg-yellows-color: #fde9d7;
  --green-opacity-bg: #f1ede1;
  --bg-bold-green: #09102a;
  --gray-input-color: #7c7c7c;
  --bg-pink-color: #cf9897;
  --gray-Dark-Dark-Mode-50: #5f626b;
  --primary-Blue-color-400: #3460dc;
  --bg-tooltip-color: #208c64;
  --bg-tranparent-green: rgba(167, 189, 181, 0.2);
  --bg-pink-blur: #f6eaea;
  --rbga--pink-bold: rgba(127, 90, 90, 1);
  --bg-gray-color-letter-page: rgba(207, 207, 207, 1);
  --color-bold-pink: #7c5b5b;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  overflow: hidden;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
  scroll-padding: 0;
  overflow-y: scroll;
}

body {
  margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Playfair Display', sans-serif;
  color: rgba(88, 88, 88, 1);
  font-size: var(--font-size-default);
}

body::-webkit-scrollbar {
  height: 100%;
  width: 100%;
  background: var(--gray-color-3);
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb {
  background: var(--pink-color-1);
  border-radius: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-main {
  background: var(--white-color);
  box-shadow: 0px 13px 37px rgba(0, 0, 0, 0.21);
}

.lateef {
  font-family: 'Lateef', sans-serif;
}

.wind-song {
  font-family: 'WindSong', cursive;
}

.quick-sand {
  font-family: 'Quicksand', sans-serif;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

hr {
  border: none;
  border-top: 1px solid var(--clr-grey-8);
}

h1,
h2,
h3,
h4,
h5 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 12px;
}

h1 {
  /* 32px */
  font-size: 1.875rem;
}

h2 {
  /* 20px */
  font-size: 1.25rem;
}

h3 {
  font-size: 1rem;
  /* 16px */
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 0.875rem;
}

p {
  margin-bottom: 0.75rem;
  color: var(--clr-grey-3);
}

/*  global classes */
.dotted_scroll {
  position: fixed;
  right: 20px;
  top: 0;
  z-index: 9999;
  bottom: 0;
  margin: auto;
  display: flex;
}

.bg-letter-main-color {
  background-color: var(--letter-main-color);
}

.dotted_scroll ul {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.dotted_scroll ul li a {
  width: 10px;
  height: 10px;
  border-radius: 10px;
  margin-bottom: 15px;
  background: var(--white-color);
  display: block;
  cursor: pointer;
  transition: 0.4s width, 0.4s height;
}

.dotted_scroll ul li a.active {
  background-color: var(--bg-pink-color);
  width: 15px;
  height: 15px;
  transition: 0.4s width, 0.4s height;
}

.slide-container {
  position: relative;
}

.slide-container img {
  object-fit: cover;
  opacity: 0.8;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
  filter: brightness(0.5);
}

.main_tit .tit_type2 {
  font-style: italic;
  font-weight: 400;
  font-size: 40px;
  line-height: 130%;
  color: var(--white-color);
  text-shadow: 2px 2px 4px var(--black-color);
}

.second-text-pink {
  font-size: 1.25rem;
  font-weight: 400;
  color: var(--text-second);
}

.icon-container {
  position: relative;
}

.icon-absolute {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.link-map {
  display: block;
}

.btn-map {
  position: relative;
  background-color: #2968c7;
  color: white;
  border-radius: 40px;
}

.gg-map-icon {
  position: absolute;
  top: 50%;
  transform: translate(50%, -50%);
}

.border-section-1 {
  border-bottom: var(--border-section-1);
}

.border-section-main {
  border-bottom: var(--border-section-main);
}

.border-section {
  border-top: var(--border-section-main);
}

.margin-auto {
  margin: auto;
}

.border-gray-item-color {
  border: var(--gray-item-color);
}

.bg-white {
  background-color: white;
}

.border-img-dash {
  border: var(--border-img-dash);
}

/* layout restructor */
.letter-wrapper {
  scroll-behavior: smooth;
}

.letter-wrapper .animate__zoomInDown {
  animation-fill-mode: none;
}

.letter-layout {
  max-width: 560px;
  margin: auto;
  position: relative;
  box-shadow: 0px 13px 37px rgba(0, 0, 0, 0.21);
}

.image {
  position: absolute;
  z-index: 99999;
}

/* .letter-layout {
  -webkit-animation-name: gototiop;
  animation-name: gototiop;
  animation-duration: 0.5s;
} */

.letter-layout .modal-content {
  overflow: scroll;
}

.letter-layout .modal-content::-webkit-scrollbar {
  display: none;
}

.slider_cmt .close_icon {
  display: none;
}

@keyframes gototiop {
  0% {
    transform: translate(0, 100%);
  }

  60% {
    transform: translate(0, 50%);
  }

  to {
    transform: translate(0, 0);
  }
}

.letter-input {
  background-color: #c6c6c6;
}

.nav-btn-container {
  top: 0;
  right: 0;
}

.section {
  overflow: hidden;
  scroll-snap-align: start;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  scroll-snap-stop: normal;
  height: 100vh;
}

.step2_list {
  display: flex;
  gap: 20px;
  margin-top: 60px;
}

.step2_list li {
  cursor: pointer;
  position: relative;
  display: flex;
  gap: 10px;
  align-items: center;
}

.step2_list li .tooltip {
  display: none;
  position: absolute;
  bottom: 145px;
  left: -100%;
  right: 0;
  padding: 5px 5px 7px;
  border-radius: 8px;
  background-color: var(--bg-tooltip-color);
  font-size: 0.8em;
  line-height: 1.5;
  text-indent: 0;
  color: var(--white-color);
  text-align: Center;
  width: 300px;
  z-index: 9999;
  transform: translate(15%, 0);
  font-weight: 400;
}

.step2_list li .tooltip::after {
  content: '';
  position: absolute;
  width: 0px;
  height: 0px;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid var(--bg-tooltip-color);
  bottom: -5px;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.step2_list li:hover .tooltip {
  display: block;
}

.step2_list li:hover .tooltip strong {
  display: block;
  font-size: 17px;
}

.box_3phone_wrap_iconn_section .step2_list li .tooltip {
  bottom: 105px;
  left: -158%;
  background-color: var(--bg-pink-color);
  font-family: var(--quick-sand-font-family);
}

.box_3phone_wrap_iconn_section .step2_list li .tooltip::after {
  border-top: 6px solid var(--bg-pink-color);
}

.wrapbox_image_pc {
  background: url('../src/assets/home-image/bgsection.png') no-repeat;
  background-size: cover;
  background-position: bottom;
  padding: 40px;
  display: flex;
  height: 100%;
  position: relative;
}

.wrapbox_image_pc .head {
  text-align: center;
}

.wrapbox_image_pc .head h3 {
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 150%;
  color: var(--white-color);
}

.image_single_pc {
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 65%;
  display: block;
}

.image_single_pc img {
  width: 100%;
}

.box_3phone_section {
  display: flex;
  align-items: center;
  background: var(--bg-pink-blur);
}

.box_3phone_wrap_image {
  display: flex;
  max-width: calc(100% - 200px);
  margin: 0 auto;
}

.box_3phone_wrap_image img {
  width: 100%;
}

.box_3phone_section .head {
  position: absolute;
  bottom: 50px;
  max-width: 400px;
  z-index: 9;
}

.box_3phone_section .head h3 {
  color: var(--black-color);
  font-size: 30px;
  margin-bottom: 0;
}

.box_3phone_section .step2_list {
  margin-top: 10px;
}

.choosetypeblock_box {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background-color: var(--gray-color-2);
}

.box_top_footer {
  position: relative;
  overflow: hidden;
  background: url('../src/assets/home-image/IMG_BgTopFooter.png') no-repeat;
  background-size: cover;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.box_top_footer .box_wrap .text {
  text-align: center;
}

.box_top_footer .box_wrap .text p {
  font-style: normal;
  font-weight: 200;
  font-size: 32px;
  line-height: 150%;
  color: var(--white-color);
}

.box_top_footer .box_wrap .text h3 {
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 150%;
  color: var(--white-color);
}

/* SCHEDULE */
.schedule-container {
  border-bottom: 1px solid var(--main-color);
  border-width: 80%;
}

.schedule-detail-img {
  margin-left: auto;
  min-width: 40%;
  margin-right: auto;
}

.schedule-bg-lr {
  min-width: 50px;
}

.schedule-bg-l {
  margin-right: -16px;
}

.schedule-bg-r {
  margin-left: -16px;
}

.schedule-bg {
  min-height: 100%;
  min-width: 100%;
}

.calendar {
  max-width: 320px;
  margin: 24px auto 24px;
  /* box-shadow: 1px 2px 3px 4px rgba(0, 0, 0, 0.1); */
  border-radius: 6px;
}

.header {
  display: flex;
  border-radius: 6px 6px 0 0;
  font-size: 20px;
  font-weight: 400;
  align-items: center;
  justify-content: center;
  gap: 40px;
}

.header h1 {
  font-family: var(--quick-sand-font-family);
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  color: var(--bg-pink-color);
}

.buttton_header {
  position: fixed;
  right: 15px;
  top: 15px;
  z-index: 9999;
}

.nav {
  width: 30px;
  height: 30px;
  border: 1px solid #fff;
  border-radius: 99px;
  outline: 0;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
}

.datetime {
  margin: 0 auto;
  font-size: 20px;
}

.week-container,
.day-container {
  display: flex;
  flex-wrap: wrap;
}

.week-cell,
.day-cell {
  flex: 0 0 calc(100% / 7);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45.7px;
  width: 40px;
}

.day-cell {
  opacity: 0.4;
  font-family: 'Darker Grotesque', sans-serif;
}

.day-cel--faded {
  opacity: 0.4;
}

.day-cel--today {
  border-radius: 99px;
  background-color: #1abc9c;
  color: #fff;
}

.day-cell--in-month {
  opacity: 1;
  color: #585858;
  font-weight: bold;
}

.active-date {
  background-color: #f1a09f;
  color: white;
  border-radius: 50%;
}

.namelocationOfWedding {
  display: flex;
  width: 100%;
  justify-content: center;
  color: #000;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 20px 0 10px;
  gap: 3px;
  align-items: baseline;
}

.namelocationOfWedding img {
  transform: translate(0, 3px);
}

.addlocationOfWedding {
  font-family: 'Darker Grotesque', sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.interface {
  width: 100%;
  text-align: center;
}

.interface h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 30px 0;
  color: #585858;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.interface_title {
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-bottom: 10px;
  text-align: center;
}

.interface_groom {
  color: #91ADA3;
}

.interface_bride {
  color: #F38723;
}

.family {
  text-align: center;
}

.family p {
  font-family: 'Darker Grotesque', sans-serif;
  margin-bottom: 5px;
  color: #585858;
  text-align: center;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  position: relative;
}

.family p img {
  position: absolute;
  left: -30px;
}

