/*  CONGRATULATION SECTION */
.href-call {
  display: flex;
  position: relative;
  justify-content: center;
  margin-left: 1.875rem;
}

.phone-container {
  position: relative;
}

.href-icon {
  position: absolute;
  animation: phoneCall 2s infinite;
  /* top: -30px; */
  left: -30px;
  width: 20px;
  /* transform: translateY(-50%); */
}

@keyframes phoneCall {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

.btn-congrat-main,
.btn-congrat-second {
  border-radius: 1rem;
  color: white;
}

/* FOOTER SECTION */
.footer-container {
