}

.footer-icon-top {
  position: absolute;
  top: -2px;
  left: 0;
}

.footer-icon-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
}

.footer-qoute {
  position: absolute;
  bottom: 8rem;
}

.qoute-footer-top,
.qoute-footer-bottom {
  color: white;
  position: absolute;
  height: 50px;
}

.qoute-footer-top {
  top: -4.5rem;
  left: 0;
}

.qoute-footer-bottom {
  bottom: 0rem;
  right: 2rem;
}

.pt-96 {
  padding-top: 40rem;
}

.effect_light {
  position: relative;
  top: 0;
}

.footer-logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  overflow: hidden;
}

.footer-logo img {
  object-fit: cover;
}

.letterPageShow.py-20 {
  padding-top: 2rem;
  padding-bottom: 25rem;
}

.loading_image {
  position: absolute;
  background: #08080885;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  color: var(--white-color);
}

.lds-roller {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.lds-roller div {
  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}

.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #fff;
  margin: -4px 0 0 -4px;
}

.lds-roller div:nth-child(1) {
  animation-delay: -0.036s;
}

.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}

.lds-roller div:nth-child(2) {
  animation-delay: -0.072s;
}

.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}

.lds-roller div:nth-child(3) {
  animation-delay: -0.108s;
}

.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}

.lds-roller div:nth-child(4) {
  animation-delay: -0.144s;
}

.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}

.lds-roller div:nth-child(5) {
  animation-delay: -0.18s;
}

.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}

.lds-roller div:nth-child(6) {
  animation-delay: -0.216s;
}

.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}

.lds-roller div:nth-child(7) {
  animation-delay: -0.252s;
}

.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}

.lds-roller div:nth-child(8) {
  animation-delay: -0.288s;
}

.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}

@keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}



/* MODAL COMPONENT */
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999999;
  visibility: hidden;
  width: 90vw;
  animation: opacity 0.3s linear;
}

.modal.active {
  visibility: visible;
  transform: all 0.3s linear;
  opacity: 1;
  top: 0;
}

.modal-container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #333;
  opacity: 0.3;
  z-index: 5;
}

.modal-block {
  display: flex;
  align-items: center;
  justify-content: center;
  /* width: 100vw; */
  position: relative;
  height: 100vh;
  overflow: scroll;
}

.modal-block::-webkit-scrollbar {
  display: none;
}

.modal-content {
  background-color: white;
  z-index: 5;
  position: relative;
  margin: auto;
  max-width: 400px;
  text-align: center;
  overflow: auto;
}

.close-modal-icon {
  position: absolute;
  top: 0.25rem;
  right: 0.25rem;
  z-index: 10;
  width: 1.6rem;
  cursor: pointer;
}

.close-modal-icon svg {
  filter: brightness(0);
}

.img_upload_box .flex,
.img_upload_box .relative,
.img_upload_box .image-item img {
  width: 100%;
}

.renderContentModal {
  font-family: var(--quick-sand-font-family);
  padding-top: 5px;
}

.renderContentModal .head {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.renderContentModal h2 {
  margin-bottom: 0;
  font-weight: 600;
  font-size: 20px;
}

.renderContentModal img {
  width: 30px;
}

.renderContentModal .contentModal {
  font-weight: 600;
  margin-bottom: 20px;
}

.renderContentModal .contentModal p {
  color: var(--black-color);
  margin-bottom: 0;
}

.index-image {
  position: absolute;
  top: 4px;
  left: 4px;
  color: white;
  z-index: 10;
  /* transform: translateX(-50%); */
