.fadeIn1 {
  animation-name: fadeIn1;
  -webkit-animation-name: fadeIn1;

  animation-duration: 5s;
  -webkit-animation-duration: 5s;

  animation-timing-function: ease-in-out;
  -webkit-animation-timing-function: ease-in-out;

  visibility: visible !important;
}

@keyframes fadeIn1 {
  0% {
    opacity: 0;
  }

  20%,
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn1 {
  0% {
    opacity: 0;
  }

  20%,
  100% {
    opacity: 1;
  }
}

.fadeIn2 {
  animation-name: fadeIn2;
  -webkit-animation-name: fadeIn2;

  animation-duration: 5s;
  -webkit-animation-duration: 5s;

  animation-timing-function: ease-in-out;
  -webkit-animation-timing-function: ease-in-out;

  visibility: visible !important;
}

@keyframes fadeIn2 {

  0%,
  20% {
    opacity: 0;
  }

  40%,
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn2 {

  0%,
  20% {
    opacity: 0;
  }

  40%,
  100% {
    opacity: 1;
  }
}

.fadeIn3 {
  animation-name: fadeIn3;
  -webkit-animation-name: fadeIn3;

  animation-duration: 5s;
  -webkit-animation-duration: 5s;

  animation-timing-function: ease-in-out;
  -webkit-animation-timing-function: ease-in-out;

  visibility: visible !important;
}

@keyframes fadeIn3 {

  0%,
  40% {
    opacity: 0;
  }

  60%,
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn3 {

  0%,
  40% {
    opacity: 0;
  }

  60%,
  100% {
    opacity: 1;
  }
}

@keyframes upanddown2 {
  0% {
    bottom: 80px;
  }

  100% {
    bottom: 60px;
  }
}

@-webkit-keyframes upanddown2 {
  0% {
    bottom: 260px;
  }

  100% {
    bottom: 240px;
  }
}

@keyframes fadeInLeftContent {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-300%, 0, 0);
    transform: translate3d(-300%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInLeftPhone {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(300%, 0, 0);
    transform: translate3d(300%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInLeftmd {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(300%, 0, 0);
    transform: translate3d(300%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(-50%, 0, 0);
    transform: translate3d(-50%, 0, 0);
  }
}

@keyframes fadeInLeftsm {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(300%, 0, 0);
    transform: translate3d(300%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0%, 0, 0);
    transform: translate3d(0%, 0, 0);
  }
}

@keyframes fadeInLeftxs {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(300%, 0, 0);
    transform: translate3d(300%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.animate__fadeInLeft_phone {
  -webkit-animation: fadeInLeftPhone;
  animation: fadeInLeftPhone;
  animation-duration: 2s;
}

.animate__fadeInLeft_Content {
  -webkit-animation: fadeInLeftContent;
  animation: fadeInLeftContent;
  animation-duration: 2s;
}

